.login {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: calc(100vh - 64px);
}
@media screen and (min-width: 992px) {
  .login {
    height: calc(100vh - 80px);
    min-height: 520px;
  }
}
.login .form {
  display: grid;
  gap: 14px;
}
@media screen and (min-width: 992px) {
  .login .form {
    gap: 16px;
  }
}
.login .form input {
  width: 100%;
}
.login .form__label {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.375;
  color: var(--front-secondary);
}
@media screen and (min-width: 992px) {
  .login .form__label {
    font-weight: 400;
    font-size: 18px;
    line-height: 1.3334;
  }
}
.login__logo {
  width: 228px;
  height: 84px;
  margin-bottom: 16px;
}
@media screen and (min-width: 992px) {
  .login__logo {
    width: 242px;
    height: 89px;
  }
}
.login__img {
  display: block;
  width: 100%;
  height: 100%;
}
.login__inner {
  display: grid;
  gap: 18px;
  width: 100%;
  max-width: 478px;
  margin: auto;
}
.login__title {
  font-weight: 600;
  font-size: 21px;
  line-height: 1.2858;
  margin: 0;
  color: var(--front-accent);
  text-align: center;
}
.login__link {
  color: inherit;
  text-decoration: none;
  outline: 0;
  transition: color var(--transition-default);
  color: var(--front-tertiary);
  text-align: center;
}
@media (hover: hover) {
  .login__link:hover {
    color: var(--front-accent);
  }
}

.iti {
  --iti-path-flags-1x: url("{{img}}/flags.webp");
  --iti-path-flags-2x: url("{{img}}/flags@2x.webp");
  --iti-path-globe-1x: url("{{img}}/av-globe.webp");
  --iti-path-globe-2x: url("{{img}}/av-globe@2x.webp");
